
HTML, BODY {
  margin: 0;
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  height: 100%;
  height: 100%;
  width: 100%;
}
.squares {
  width: 342px;
  margin-left: auto;
  margin-right: auto;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-direction: row;
          flex-direction: row;
}
.squares DIV.title-button {
    font-size: 1em;
    line-height: 3em;
    background: #33f;
    color: #fff;
    cursor: default;
}
.squares DIV {
    -webkit-box-shadow: 2px 2px 5px #888888;
            box-shadow: 2px 2px 5px #888888;
    cursor: pointer;
    font-size: 3.5em;
    font-weight: normal;
    padding-top: 26px;
    line-height: 1em;
    width: 100px;
    border: 1px solid #ccc;
    border-radius: 10px;
    height: 80px;
    margin: 6px;
}
.squares DIV.review-button {
    width: 330px;
}
DIV.arrow {
  margin-top: 0;
  font-size: 80px;
  color: #00f;
  cursor: pointer;
  border: none;
  margin-left: auto;
  margin-right: auto;
  -webkit-box-shadow: none;
          box-shadow: none;
  font-weight: normal;
}
.fa-icon {
  width: auto;
  height: 1em;
}
.fa {
  font-size: 70px;
}
.type-ahead-group {
  position: relative;
}
.type-ahead-group > input {
  background-color: #FFFFFF;
  background-image: none;
  border-radius: 4px;
  border: 1px solid #CCCCCC;
  -webkit-box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.075);
          box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.075);
  color: #555555;
  font-size: 24px;
  line-height: 1.42857143;
  padding: 6px 12px;
}
.type-ahead-group > ul {
  background-color: #FFFFFF;
  border-radius: 4px;
  border: 1px solid rgba(0, 0, 0, 0.1);
  -webkit-box-shadow: 0 6px 12px rgba(0, 0, 0, 0.17);
          box-shadow: 0 6px 12px rgba(0, 0, 0, 0.17);
  display: none;
  float: left;
  font-size: 24px;
  left: 20px;
  list-style: none;
  margin: 2px 0 0;
  min-width: 170px;
  padding: 5px 0;
  position: absolute;
  text-align: left;
  top: 100%;
  z-index: 1000;
}
.type-ahead-group > ul > li > a {
  clear: both;
  color: #333;
  cursor: default;
  display: block;
  font-weight: 400;
  line-height: 1.42857143;
  padding: 3px 20px;
  white-space: nowrap;
}
.type-ahead-group > ul > li.active > a, .type-ahead-group > ul > li > a:hover {
  background-color: #3F9BFA;
  color: #FFFFFF;
  text-decoration: none;
}



