
.dot {
  -webkit-box-shadow: 1px 1px 3px #888888;
          box-shadow: 1px 1px 3px #888888;
  width: 1.4em;
  height: 1.4em;
  border-radius: 4px;
  border: 2px solid green;
  display: inline-block;
  margin: 3px;
  margin-bottom: 0;
}
.dotbar {
  width: 240px;
  margin-top: 3px;
  margin-bottom: 4px;
  margin-left: auto;
  margin-right: auto;
}
.ball {
  background: green;
}
.dice {
  width: 120px;
  height: 120px;
  margin-left: auto;
  margin-right: auto;
  background: #fff;
  margin-top: 0;
  margin-bottom: 0;
  border: 3px solid #666;
  border-radius: 10px;
}
.dice TD {
    padding: 4px;
    background: #fff;
    border: 8px solid #fff;
}
.dice .d {
    background: blue;
}



