
.dsquares {
  width: 360px;
  margin-left: auto;
  margin-right: auto;
}
DIV.dtable {
  display: inline-block;
  height: 122px;
  width: 122px;
}
.smenu TABLE.dice {
  border: 1px solid #ccc;
}
DIV.arrow {
  font-size: 80px;
  width: 100px;
  height: 100px;
  line-height: 1.8;
}
.passed {
  background: green;
}
.srow {
  border: 1px solid #ccc;
  background: #33f;
  border-radius: 10px;
  padding-top: 10px;
  margin-bottom: 8px;
}
.passed {
  background: green;
}



